.search-form {
  padding: 16px;
  display: flex;
  justify-content: center;
  background-color: #1fa1f81a;
}
.search-input {
  display: block;
  outline: none;
  padding: 8px;
  margin-right: 12px;
  border: 1px solid gray;
  border-radius: 4px;
}
.search-input:focus {
  border: 1px solid gray;
  box-shadow: 2px 4px 3px rgba(194, 192, 192, 0.521), -2px 3px 3px rgba(194, 192, 192, 0.575),
    3px 3px 3px rgba(194, 192, 192, 0.603);
}

.button {
  color: gray;
  background-color: #fff;
  font-weight: 600;
  border: 1px solid gray;
  border-radius: 4px;
  padding: 8px 16px;
  //   transition: color 2500ms ease-in;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.button:before {
  content: '\f002';
  font-family: FontAwesome;
  font-size: 16px;
  color: gray;
  margin-right: 8px;
}
.button:hover {
  color: rgb(31, 161, 248);
  box-shadow: 2px 4px 2px rgb(194, 192, 192), -2px 3px 2px rgb(194, 192, 192),
    3px 3px 3px rgb(194, 192, 192);
}
